import React, { useState, useEffect } from 'react';


function ProductSlider({ products }) {
  const [currentSlide, setCurrentSlide] = useState(0);

  useEffect(() => {
    if (products && products.length > 0) { // Check if products is not undefined and has items
      const slideInterval = setInterval(() => {
        setCurrentSlide((currentSlide + 1) % products.length);
      }, 3000);
      return () => clearInterval(slideInterval);
    }
  }, [currentSlide, products]);

  if (!products || products.length === 0) { // Show a message if products is undefined or empty
    return ;
  }

  return (
    <div className="slider-container">
      {products.map((product, index) => (
        <div
          key={index}
          className={`slide ${index === currentSlide ? 'active' : ''}`}
        >
          <img src={product.image} alt={product.name} />
          <h3>{product.name}</h3>
          <p>{product.description}</p>
        </div>
      ))}
    </div>
  );
}

export default ProductSlider;




